$icon-progress-empty: "\e906" !default;
$icon-progress-two: "\e907" !default;
$icon-clock: "\e903" !default;
$icon-clock-seconds: "\e902" !default;
$icon-volume_off: "\e904" !default;
$icon-volume_up: "\e905" !default;
$icon-clock-bold: "\e901" !default;
$icon-clock-bold-filled: "\e900" !default;

$font-family: "icomoon" !default;
$font-file-name: $font-family + "-pomodoro.woff" !default;

$woff-dataURI: "data:application/x-font-woff;base64,";

$woff-uri: if(variable_exists(font-path), "#{$font-path}/#{$font-file-name}", $woff-dataURI);

@font-face {
	font-family: $font-family;
	src: url($woff-uri) format('woff');
	font-weight: normal;
	font-style: normal;
}

span::before {
	input[type="checkbox"] + & {
		.icon > & {
			/* use !important to prevent issues with browser extensions that change fonts */
			font-family: $font-family !important;
			speak: none;
			font-style: normal;
			font-weight: normal;
			font-variant: normal;
			text-transform: none;
			line-height: 1;

			border: 1px solid gray;
			border-radius: 20%;
			padding: 2px;

			/* Better Font Rendering =========== */
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}
		.icon-progress > & {
			content: $icon-progress-two;
		}
		.icon-clock-seconds > & {
			content: $icon-clock-seconds;
		}
		.icon-clock-filled > & {
			content: $icon-clock-bold-filled;
		}
		.icon-volume > & {
			content: $icon-volume_up;
		}
	}

	input[type="checkbox"]:checked + & {
		.icon-progress > & {
			content: $icon-progress-empty;
		}
		.icon-clock-seconds > & {
			content: $icon-clock;
		}
		.icon-clock-filled > & {
			content: $icon-clock-bold;
		}
		.icon-volume > & {
			content: $icon-volume_off;
		}
	}
}
